.title-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 15px;
    padding: 32px;
    border-bottom: 1px solid var(--black);
}

.title {
    color: var(--black);
    font-weight: 600;
    font-size: 56px;
}

.green-text {
    color: var(--green);
}

.title-link {
    color: var(--black);
    font-weight: 400;
    font-size: 30px;
    text-transform: uppercase;

}